.side-menu {
	margin-bottom: 0;
	padding-bottom: 40px;
	padding: 0;
}

.side-menu__item {
	position: relative;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	padding: 12px 19px 12px 34px;
	font-size: 14px;
	font-weight: 400;
	-webkit-transition: border-left-color 0.3s ease, background-color 0.3s ease;
	-o-transition: border-left-color 0.3s ease, background-color 0.3s ease;
	transition: border-left-color 0.3s ease, background-color 0.3s ease;
	color: #dbdaea;
	&.active,
	&:hover,
	&:focus {
		text-decoration: none;
		background: $white-1;
	}
}

.slide-menu a.active {
	background: $white;
}

.slide-item {
	&.active,
	&:hover,
	&:focus {
		text-decoration: none;
		color: $primary !important;
	}
}

.slide.is-expanded a {
	/*color: #0052cc;*/
	text-decoration: none;
}

.side-menu .side-menu__icon {
	font-size: 16px;
	height: 30px;
	line-height: 30px;
	text-align: center;
	vertical-align: middle;
	width: 30px !important;
	border-radius: 50%;
	margin-right: 8px;
	margin-left: -8px;
}

.side-menu__icon {
	-webkit-box-flex: 0;
	-ms-flex: 0 0 auto;
	flex: 0 0 auto;
	display: inline-block;
	font-size: 1rem;
	position: relative;
	width: 40px;
	height: 40px;
	line-height: 40px;
	margin: 0 auto;
	border-radius: 7px;
	background: #160472;
	border: 1px solid rgb(88, 72, 187);
}

.side_menu_img {
	position: absolute;
	height: 20px;
	margin: 11px auto;
	left: 0;
	right: 0;
	line-height: 8px;
	text-align: center;
	align-items: center;
	line-height: 29px;
	flex: 0 0 auto;
	transform: rotate(-45deg);
	opacity: 0.8;
}

.side-menu__label {
	white-space: nowrap;
	-webkit-box-flex: 1;
	-ms-flex: 1 1 auto;
	flex: 1 1 auto;
	display: block;
	text-align: center;
	font-weight: 400;
}

.slide.is-expanded {
	.slide-menu {
		max-height: 100%;
	}
	.angle {
		-webkit-transform: rotate(90deg);
		-ms-transform: rotate(90deg);
		transform: rotate(90deg);
	}
}

.slide-menu {
	max-height: 0;
	overflow: hidden;
	-webkit-transition: max-height 0.3s ease;
	-o-transition: max-height 0.3s ease;
	transition: max-height 0.3s ease;
	padding: 0;
	font-size: .8rem !important;
}

.slide-item {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	padding: 7px;
	font-size: 14px;
	color: #191919;
	border-bottom: 0;
	&:hover {
		border-bottom: 0 !important;
	}
	.icon {
		margin-right: 5px;
	}
}

.angle {
	-webkit-transform-origin: center;
	-ms-transform-origin: center;
	transform-origin: center;
	-webkit-transition: -webkit-transform 0.3s ease;
	transition: -webkit-transform 0.3s ease;
	-o-transition: transform 0.3s ease;
	transition: transform 0.3s ease;
	transition: transform 0.3s ease, -webkit-transform 0.3s ease;
	color: #191919 !important;
}

