	}
	li a {
		color: #191919;
		display: block;
	}
}

.header-secondary {
	margin: 0 -12px;
	border-bottom: 1px solid $border;
}

.sidetab-menu {
	.panel-tabs {
		width: 100%;
		li {
			border: 1px solid $border;
			margin: 2px;
			&:last-child {
				margin-right: 0;
			}
			&:first-child {
				margin-left: 0;
			}
		}
	}
	.side-tab:before {
		display: none;
	}
	.panel-tabs li {
		a {
			padding: 7px 12px 7px 12px;
			color: #1c1d22;
			display: block;
			font-size: 11px;
			text-align: center;
			i {
				display: block;
				text-align: center;
				font-size: 16px !important;
				margin-bottom: 0.1rem;
			}
			&.active {
				color: $white;
				border: none;
				position: relative;
				&:before {
					content: '';
					position: absolute;
					width: 10px;
					height: 6px;
					bottom: -1px;
					margin: 0 auto;
					left: 0;
					right: 0;
					z-index: 1;
					display: block;
					border-top-left-radius: 50px;
					border-top-right-radius: 50px;
					background: $white;
				}
			}
			&:hover:before {
				content: '';
				position: absolute;
				width: 10px;
				height: 6px;
				bottom: -1px;
				margin: 0 auto;
				left: 0;
				right: 0;
				z-index: 1;
				display: block;
				border-top-left-radius: 50px;
				border-top-right-radius: 50px;
				background: $white;
			}
		}
		&:hover {
			border-right: 1px solid $border !important;
		}
		a:hover {
			color: $white;
			position: relative;
		}
	}
}

.sidenav-toggled .resp-vtabs li.active:before {
	display: none;
}

@media (min-width: 992px) {
	.header.app-header .form-inline {
		margin-left: 0;
	}
	.toggle {
		margin-left: 13rem;
	}
	.color-headerlogo {
		margin-left: 1rem;
	}
}

@media (max-width: 568px) {
	.app-content {
		margin-top: 9px;
	}
}

.header.app-header .form-control.header-search {
	margin-top: 3px;
}

.first-sidemenu {
	background: #17028a;
}